<template>
  <v-app id="inspire">
    <v-app-bar app>
      <v-toolbar-title>Loyalty Cards</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-row class="justify-end" row>
        <v-dialog v-model="dialog" persistent max-width="600px">
          <template v-slot:activator="{ on, attrs }">
            <v-btn color="info" dark v-bind="attrs" v-on="on">
              create Card
            </v-btn>
          </template>
          <v-card>
            <v-card-title>
              <span class="text-h5">User Profile</span>
            </v-card-title>
            <v-card-text>
              <v-container>
                <v-row>
                  <v-col cols="12" class="text-center">
                    <v-text-field
                      label="Card Number*"
                      v-model="editedItem.card_number"
                    ></v-text-field>
                    <v-text-field
                      label="First Name*"
                      v-model="editedItem.first_name"
                    ></v-text-field>
                    <v-text-field
                      label="Last Name*"
                      v-model="editedItem.last_name"
                    ></v-text-field>
                    <v-select
                      :items="['Gold', 'Platinum', 'Silver']"
                      label="Membership Tier *"
                      v-model="editedItem.membership_tier"
                    ></v-select>
                    <v-textarea
                      background-color="grey lighten-2"
                      label="Description of the user "
                      v-model="editedItem.Description"
                    ></v-textarea>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="red darken-1" text @click="dialog = false">
                Close
              </v-btn>
              <v-btn color="blue darken-1" text @click="save"> Save </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-row>
      <v-spacer></v-spacer>
    </v-app-bar>
    <v-main>
      <v-container>
        <v-row>
          <v-col v-for="user in posts" :key="user" cols="4">
            <v-card class="mx-auto" shaped>
              <v-card-text class="red--text text--lighten-1">
                {{ user.card_number }}
              </v-card-text>
              <v-card-text>
                <div>User Loyilty Cards</div>
                <p class="text-h4 text--primary">
                  {{ user.first_name }} {{ user.last_name }}
                </p>
                <p>{{ user.membership_tier }}</p>
                <div class="text--primary">
                  {{ user.Description }}<br />
                  "Thanks you For Shoping with us."<b
                    class="green--text text--darken-1"
                  >
                    {{ user.first_name + " " + user.last_name }}</b
                  >
                </div>
              </v-card-text>
              <v-spacer></v-spacer>
              <br />
              <one-card :users="user"> </one-card>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </v-main>
  </v-app>
</template>
<script>
import OneCard from "./OneCard.vue";
import axios from "axios";
window.axios = require("axios");
// import CardsAdd from "./CardsAdd.vue";
export default {
  name: "Cards",
  components: { "one-card": OneCard },
  mounted: function () {
    axios
      .get("https://mocki.io/v1/6dcbbb3d-3487-4955-a0e7-33170e76c772")
      .then((response) => (this.posts = response.data));
  },
  data() {
    return {
      posts: null,
      dialog: false,
      editedIndex: -1,
      editedItem: {
        first_name: "",
        last_name: "",
        membership_tier: ["Gold", "Platinum", "Silver"],
        Description: "",
        card_number: 0,
        posts:[]
      },

    };
  },
  closeDelete() {
    this.dialogDelete = false;
    this.$nextTick(() => {
      this.editedItem = Object.assign({}, this.defaultItem);
      this.editedIndex = -1;
    });
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },
  created() {
    this.initialize();
  },

  methods: {
    initialize() {
      this.posts = [];
    },
    save() {
      if (this.editedIndex > -1) {
        Object.assign(this.posts[this.editedIndex], this.editedItem);
      } else {
        this.posts.push(this.editedItem);
      }
      this.close();
    },
  },
  close() {
    this.dialog = false;
    this.$nextTick(() => {
      this.editedItem = Object.assign({}, this.defaultItem);
      this.editedIndex = -1;
    });
  },
};
</script>
<style>
.v-card--reveal {
  bottom: 0;
  opacity: 1 !important;
  position: absolute;
  width: 100%;
}
</style>
