<template>
  <v-row justify="center">
    <v-dialog v-model="dialog" persistent max-width="290">
      <template v-slot:activator="{ on, attrs }">
        <v-btn color="light-green" dark v-bind="attrs" v-on="on">
          View More
        </v-btn>
      </template>
      <v-card shaped>
        <v-card-title class="text-h4">User Details </v-card-title>

        <v-card-text>
          <b> ID: {{ users.card_number }} </b>
        </v-card-text>
        <v-card-text>
          <b>name:</b> {{ users.first_name }} {{ users.last_name }}
        </v-card-text>
        <v-card-text>
          <v-btn color="lime darken-1" text>{{ users.membership_tier }}</v-btn>
            <v-icon color="red">mdi-hexagram</v-icon>
        </v-card-text>

        <v-card-text
          >Let Google help apps determine location. This means sending anonymous
          location data to Google, even when no apps are running.</v-card-text
        >

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="red darken-1" text @click="dialog = false">
            Close
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-row>
</template>
<script>
export default {
  props: ["users"],
  data() {
    return {
      dialog: false,
    };
  },
};
</script>